<template>
  <div class="page-actionsheet">
    <h1 class="page-title">Action Sheet</h1>
    <div class="page-actionsheet-wrapper">
      <zmbl-button @click.native="sheetVisible = true" size="large">点击上拉 action sheet</zmbl-button>
      <zmbl-button @click.native="sheetVisible2 = true" size="large">不带取消按钮的 action sheet</zmbl-button>
    </div>
    <zmbl-actionsheet :actions="actions" v-model="sheetVisible"></zmbl-actionsheet>
    <zmbl-actionsheet :actions="actions2" v-model="sheetVisible2" :show-cancel="false"></zmbl-actionsheet>
  </div>
</template>

<style lang="scss">
  .page {
    &-actionsheet {
      &-wrapper {
        padding: 0 20px;
        position: absolute;
        top: 50%;
        width: 100%;
        transform: translateY(-50%);

        button:first-child {
          margin-bottom: 20px;
        }
      }
    }
  }
</style>

<script type="text/babel">
  export default {
    data() {
      return {
        sheetVisible: false,
        sheetVisible2: false,
        actions: [],
        actions2: []
      };
    },

    methods: {
      takePhoto() {
        console.log('taking photo');
      },

      openAlbum() {
        console.log('opening album');
      },

      goBack() {
        history.go(-1);
      }
    },

    mounted() {
      this.actions = [{
        name: '拍照',
        method: this.takePhoto
      }, {
        name: '从相册中选择',
        method: this.openAlbum
      }];
      this.actions2 = [{
        name: '确定'
      }, {
        name: '返回上一步',
        method: this.goBack
      }];
    }
  };
</script>
